body {
  color: #333;
  text-align: center;
  background-color: #f4f4f9;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  color: #0078d7;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #0078d7;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  transition: background-color .3s;
}

button:hover {
  background-color: #005bb5;
}

#output {
  color: #555;
  margin-top: 20px;
  font-size: 18px;
}

#info-container {
  margin-top: 20px;
}

.card {
  background: #fff;
  border-radius: 10px;
  margin: 10px auto;
  padding: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.listening {
  animation: pulse 1s infinite;
  background-color: tomato !important;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }
}

